.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-Title
{
  display: flex;
  flex-direction: row;
  
}

.App-Title div
{
  font-size: calc(60px + 2vmin);

}

.App-Title div img
{
  height: calc(60px + 2vmin);
  vertical-align: middle;
  
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.Egg-Control
{
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  border: 1px solid #7e8592;
  border-radius: 10px;
  font-size: 12px;
  text-align: left;
  margin-bottom: 20px;
  margin-top: 20px;
  padding: 0px 10px 10px 10px;

}

.Egg-Property
{
  padding: 10px;
}

.Egg-Property p
{
  font-weight: 700;
}

.App-Buttons
{
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}

.App-Buttons div
{
  margin: 5px;
}

.myButton {
	
	border-radius:28px;
	
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:16px 31px;
	text-decoration:none;
}

.Stop
{
  background-color:#c74444;
  border:1px solid #ab1818;
}

.Go
{
  background-color:#44c767;
  border:1px solid #18ab29;
}

.myButton:active {
	position:relative;
	top:1px;
}

.myButton:disabled
{
  background-color: #777777;
  color: #9a9a9e;
  border:1px solid #777777;
}